// Header

.b-header {
  @include container(88%, 0, 0 6%);
  height: 8.5rem;
  background: #000000;
  position: fixed;
  top: 0;
  z-index: 99999999;

  @include breakpoint(tabletOnly) {
    @include container(90%, 0, 0 5%);
    height: 8.5001rem;
  }

  @include breakpoint(desktopOnly) {
    @include container(96%, 0, 0 2%);
    height: 6.6rem;
  }

  & svg {
    cursor: pointer;
  }

  &__logo {
    width: 37.4vw;
    position: absolute;
    top: 2.6vw;
    left: 50%;
    margin-left: -18.5vw;
    z-index: 120;

    @include breakpoint(tabletOnly) {
      top: 0.6vw;
    }

    @include breakpoint(desktopOnly) {
      width: 11.6vw;
      margin-left: -5.8vw;
    }

    & img {
      width: 100%;
    }
  }

  &__social {
    @include breakpoint(phoneAndTablet) {
      float: none;
      position: relative;
      font-size: 6rem;
      color: $black;
      width: 50%;
      margin: 0 auto;
    }

    @include breakpoint(tabletOnly) {
      font-size: 5rem;
      width: 57%;
    }

    @include breakpoint(desktopOnly) {
      position: relative;
      font-size: 2rem;
      top: 1.4vw;
      color: #ffffff;
      float: left;
      left: -0.3vw;
      width: auto;
    }

    & svg {
      margin-right: 13vw;

      @include breakpoint(tabletOnly) {
        margin-right: 9vw;
      }

      @include breakpoint(desktopOnly) {
        margin-right: 20px;
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__menu-trigger {
    float: left;
    position: relative;
    cursor: pointer;
    top: 2.5rem;
    left: 0.2rem;
    font-size: 3.9rem;

    @include breakpoint(tabletOnly) {
      font-size: 4.1rem;
      top: 2.35rem;
      left: 0.4rem;
    }

    @include breakpoint(desktopOnly) {
      display: none;
    }

    & img {
      width: 100%;
    }
  }

  &__search {
    font-size: $fontBig;
  }
}

// Main Navigation

.c-main-navigation {
  list-style-type: none;
  margin: 0;
  padding: 0;

  @include breakpoint(phoneOnly) {
    margin-bottom: 33vw;
    position: relative;
  }

  @include breakpoint(tabletOnly) {
    margin-bottom: 28vw;
  }

  @include breakpoint(desktopOnly) {
    width: 90%;
    text-align: center;
    position: fixed;
    top: 2.9rem;
    left: 50%;
    margin-left: -44%;
    margin-bottom: 0;
  }

  &--wrap {
    @include container(77vw, 0, 22.3% 2vw 10vw 5.5vw);
    background: $white;
    position: fixed;
    top: 8.5rem;
    bottom: 0;
    left: -86vw;
    z-index: 50;
    overflow: hidden;
    transition: all 0.2s linear;

    @include breakpoint(tabletOnly) {
      @include container(61vw, 0, 16.4vw 1vw 10vw 8.4vw);
      left: -71vw;
      top: 8.5001rem;
    }

    @include breakpoint(desktopOnly) {
      height: auto;
      position: absolute;
      width: 92%;
      z-index: 110;
      background: transparent;
      padding: 0 0 0 0.3%;
      left: 1.8vw;
      top: 0;
      overflow: visible;
    }

    &.inView {
      left: 0;
    }
  }

  & a {
    @include breakpoint(phoneAndTablet) {
      text-decoration: none;
      color: $black;
    }
  }

  & > li {
    @include breakpoint(phoneAndTablet) {
      margin-bottom: 11.3vw;
      position: relative;
    }

    @include breakpoint(tabletOnly) {
      margin-bottom: 7.7vw;
    }

    @include breakpoint(desktopOnly) {
      display: inline-block;
      margin-right: 4.9rem;
      margin-bottom: 0;
      z-index: 110;

      &:first-child {
        margin-left: 7rem;
      }

      &:nth-child(3) {
        margin-right: 33rem;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    & svg {
      @include breakpoint(phoneAndTablet) {
        color: $yellow;
        margin-right: 5vw;
        position: relative;
        top: -2px;
        font-size: 1.55rem;
      }

      @include breakpoint(tabletOnly) {
        margin-right: 3vw;
      }

      @include breakpoint(desktopOnly) {
        display: none;
      }
    }
  }

  & > li > a {
    text-transform: uppercase;
    @include fontSize(28, 22.73, 12);
    @include letterSpacing(100, 100, 100);
    font-weight: 700;

    @include breakpoint(desktopOnly) {
      color: $yellow;
      text-decoration: none;
      transition: color 0.15s linear;
    }

    &.active,
    &:hover {
      @include breakpoint(desktopOnly) {
        color: $white;
        padding-bottom: 0.35rem;
        border-bottom: 3px solid $white;
      }
    }
  }

  & .sub__menu {
    display: none;
    padding-left: 12.6vw;
    @include fontSize(28, 22.69, 14);
    @include letterSpacing(100, 100, 100);

    @include breakpoint(tabletOnly) {
      padding-left: 8.6vw;
    }

    @include breakpoint(desktopOnly) {
      position: fixed;
      top: 6.6rem;
      left: 0;
      right: 0;
      background: $white;
      overflow: hidden;
      box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2);
      padding-left: 0;
    }

    & > ul {
      list-style: none;

      @include breakpoint(desktopOnly) {
        list-style-type: none;
        margin: 0;
        padding: 0;
        display: block;
        position: relative;
        width: 17.8%;
        float: left;
        padding: 4.5% 0 1% 7.2%;
        text-transform: uppercase;

        &::after {
          content: "";
          position: absolute;
          right: 0;
          top: 0;
          width: 1px;
          height: 40rem;
          background: #ececec;
        }
      }

      &:last-of-type {
        margin-bottom: 2vw;

        @include breakpoint(tabletOnly) {
          margin-bottom: 1vw;
        }

        @include breakpoint(desktopOnly) {
          margin-bottom: 0;
        }
      }

      & > li {
        margin-top: 8.1vw;

        @include breakpoint(tabletOnly) {
          margin-top: 5.6vw;
        }

        @include breakpoint(desktopOnly) {
          text-align: left;
          margin-top: 0;
          margin-bottom: 19.3%;
        }

        & > a {
          font-weight: 700;

          @include breakpoint(desktopOnly) {
            text-decoration: none;
            color: $black;
            font-size: calcRem(14);
            font-weight: 900;
            letter-spacing: calcLS(50);
          }
        }
      }
    }
  }
}

.page.menuOpened {
  left: 84vw;

  @include breakpoint(tabletOnly) {
    left: 69vw;
  }
}

@include breakpoint(phoneAndTablet) {
  body.menuOpened,
  html.menuOpened {
    overflow: hidden;
  }

  .menu__opened__overlay {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 1000;
  }
}

// Navigation-top Articles

.c-navigation-top-articles {
  @include breakpoint(phoneAndTablet) {
    display: none;
  }

  float: left;
  width: 32%;
  padding: 4% 10% 0 5.7%;

  & .heading--row {
    margin-bottom: 1.2rem;
    letter-spacing: 0;

    & .text--bolder {
      font-size: calcRem(26);
    }

    & .text--italic {
      font-size: calcRem(30);
      position: relative;
      top: -0.1rem;
      left: 0.5rem;
    }
  }

  & .row--full {
    margin-bottom: 1.8rem;
  }

  & .top__article {
    width: 45%;
    margin-right: 8%;
    text-align: left;
    float: left;

    &:last-child {
      margin-right: 0;
    }

    & img {
      float: left;
      width: 28%;
      margin-right: 9%;
    }

    & p {
      font-size: calcRem(13);
      line-height: calcRem(15);
      letter-spacing: calcLS(50);
      float: left;
      width: 63%;
      margin-top: -0.3rem;
    }
  }
}